import io from "socket.io-client";
import queryString from "query-string";
import { socket_client_url } from "./api";
import store from "../store";
import { REACTION_UPDATE, REMOVE_POST, UPDATE_POST, SPOTLIGHT_UPDATE } from "../reducers/reducersKeys";
import { themeByWallID } from "./themeActions";

export const isEditor = window.isEditor ? true : false
const wSessionStorage = () => {
    try {
        var tabID =
            sessionStorage.tabID && sessionStorage.closedLastTab !== "2"
                ? sessionStorage.tabID
                : (sessionStorage.tabID = makeid(25));
        sessionStorage.closedLastTab = "2";
        window.addEventListener("unload", () => {
            sessionStorage.setItem('closedLastTab', '1');
        });

        window.addEventListener("beforeunload", () => {
            sessionStorage.setItem('closedLastTab', '1');
        });
        return tabID;
    } catch (ex) {
        var tabID = makeid(25);
        return tabID;
    }
};
const makeid = length => Array.from({ length }, () =>
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"
        .charAt(Math.floor(Math.random() * 62))
).join('');




const active_screen_key = `active-screen`;
const post_object = `post_object_`;
const delete_feed = `deleteFeed_`;
const remove_screen_key = `remove-screen`;
const intra_active_key = `updateInteractive_`;
const GET_PREVIEW = window.location.href.includes("preview=1")
const spotlight_key = `spotlightstatus_`;


/* Connected With Socket  */
export const socialWallSocket = (dispatch) => {
    const { isStudio, wall, error_code, wallErrorCode } = store.getState().appData;
    const { screenLimitExceed, screenLimit, screens } = wall.UserRule
    if (wall && Object.keys(wall).length > 0 && wall.Wall.id) {
        let screenId = wSessionStorage();
        let queryStudio = false;
        const parsed = queryString.parse(window.location.search);
        if (parsed.studio && parsed.studio == 1) queryStudio = true;
        let screenFlag = `&screenId=${screenId}`;
        let screenLFlag = `&screenLimit=${screenLimit}&screens=${screens}&planId=${wall.UserDetail.planId}`;
        if (isStudio) {
            screenFlag = ``;
            screenLFlag = ``;
        }

        let query = isEditor
            ? ``
            : `name=signage${screenFlag}&wallId=${wall.Wall.id}&owner=${wall.Wall.owner
            }${screenLFlag}&screenLimitExceed=${screenLimitExceed}&timeStamp=${Math.floor(
                Date.now() / 1000
            )}`;

        if ((error_code === 213 || wallErrorCode === 213) && !isEditor) query = ``;

        const socket_connection = io(`${socket_client_url}`, {
            transports: ["websocket"],
            transport: ["websocket"],
            query: query,
            upgrade: true,
            reconnection: true,
            reconnectionDelay: 3000,
            reconnectionDelayMax: 5000,
            reconnectionAttempts: 2

        });

        const on_emit_socket_data = (params, callBack) => socket_connection.on(params, callBack);

        const activeScreenData = {
            screenId,
            wallID: wall.Wall.id,
            timeStamp: Math.floor(Date.now() / 1000),
            screenLimitExceed: false,
        };
        setInterval(() => socket_connection.emit(active_screen_key, activeScreenData), 30000);

        on_emit_socket_data(`${delete_feed}${wall.Wall.id}`, wallDataActionFromSocket);

        on_emit_socket_data(`${post_object}${wall.Wall.id}`, wallDataActionFromSocket);
        on_emit_socket_data(`${spotlight_key}${wall.Wall.id}`, spotlightFromScoket);


        if (!(isEditor && GET_PREVIEW) && wall && wall.Personalization && wall.Personalization.interactive) on_emit_socket_data(`${intra_active_key}${wall.Wall.id}`, ({ id, action }) => {
            dispatch({ type: REACTION_UPDATE, payload: action == "prepand" ? true : false });
            setTimeout(() => dispatch({ type: REACTION_UPDATE, payload: false }), 5000);
        });

        on_emit_socket_data(screenId, (data) => {
        });
        window.addEventListener("unload", () => {
            socket_connection.emit(remove_screen_key, { screenId, timeStamp: Math.floor(Date.now() / 1000) });
        });
        window.addEventListener("beforeunload", () => {
            socket_connection.emit(remove_screen_key, { screenId, timeStamp: Math.floor(Date.now() / 1000) });
        });
    }
};


/* Action (Refresh/Prepend)  Data from Socket  */
const wallDataActionFromSocket = (data) => {
    const { appData } = store.getState();
    const { postData } = appData;

    if (String(data.action) == "refresh" || data.action === "refreshWall") if (!isEditor) window.location.reload();
    if (String(data.action) == "prepend") {
        const newPost = data.data;
        if (newPost?.length) {
            if (postData.postData.length == 0) {
                store.dispatch(themeByWallID(appData.wallID, null));
            }
            else store.dispatch({ type: UPDATE_POST, payload: newPost });
        }
    }
    if (String(data.action) == "remove") {
        var removeIDs = []
        const PostIds = data.data.posts;
        if (PostIds && PostIds.length) {
            let PostIdString = PostIds.map((i) => Number(i));
            if (PostIdString?.length > 0 && postData.completeDataObject && Object.keys(postData.completeDataObject).length > 0) {
                Object.values(postData.completeDataObject).map((post) => {
                    const result = PostIdString.find(num => num == post.referenceId);
                    if (result) {
                        removeIDs.push(post.id)
                    }
                })
            }
            if (removeIDs?.length > 0) {
                if (postData.postData.length == removeIDs.length) store.dispatch(themeByWallID(appData.wallID, null));
                else store.dispatch({ type: REMOVE_POST, payload: removeIDs });
            }

        }
    }
};

export const spotlightFromScoket = (data) => {
    if (data && Object.keys(data).length > 0 && !window.location.href.includes("personalize")) {
        store.dispatch({ type: SPOTLIGHT_UPDATE, payload: { status: data.highlight == 1 ? true : false, data: data } });
    }

}