import { GET_APP_DATA, SHOW_MORE_DATA_UPDATE, FILTER_DATA_UPDATE, ERROR_RESPONSE_WITH_MESSAGE, GET_THEME_ERROR_SUCCESS, PLAN_LIMIT, REMOVE_POST, UPDATE_POST } from './reducersKeys'
import { ERROR, POST_DATA, LOADER, WALL_ID, LANGUAGE_SETTINGS, WALL_DATA, WEB_FILTERS, NETWORK_DATA, FILTER_TAGS, REVIEW_FILTERS } from './contants'
import { languageSettings } from '../actions/themeActions'
import { convertObjectToArray, convertObjectToArray_Tags, isPostExist } from '../utils'
import { SHARE_ROOT_URL, suiteTrack, isTagEmbed, isTagShop, isSocialWall, isSocialEmbed } from "../actions/api";
const initialState = {
    error: ERROR,
    postData: POST_DATA,
    loader: LOADER,
    wall: WALL_DATA,
    wallID: WALL_ID,
    languageSetting: LANGUAGE_SETTINGS,
    webFilters: WEB_FILTERS,
    networkData: NETWORK_DATA,
    filter_tags: FILTER_TAGS,
    feeds: null,
    reviewFilters: REVIEW_FILTERS,
    isStudio: 0,
    announcements: null
}
let isReviewStatus = false
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_APP_DATA: {
            const widgetTheme = action.themeId ? action.payload.wall.Personalization.widgetTheme = parseInt(action.themeId) : '';
            const loadData = APP_DATA(action.payload, action.heightEvent, widgetTheme, true)
            return { ...state, ...loadData }
        }
        case SHOW_MORE_DATA_UPDATE: {
            const { newData, filterNetworkId, appendData, hasMoreData, tagName } = action.payload;

            var PostUpdateData = postDataRenderData(filterNetworkId, state.postData, newData, tagName, state.filter_tags)
            PostUpdateData.afterUsedList = [...PostUpdateData.afterUsedList, PostUpdateData.appendData.after]
            PostUpdateData.hasMoreData = { ...PostUpdateData.hasMoreData, ...hasMoreData }

            let appendNewData = {
                after: appendData.after,
                networkID: appendData.networkID,
                heightEvent: appendData.heightEvent
                    ? appendData.heightEvent
                    : PostUpdateData.appendData.heightEvent,
                tagName: tagName
            };
            PostUpdateData.appendData = appendNewData;
            return {
                ...state,
                postData: PostUpdateData
            };
        }
        case FILTER_DATA_UPDATE: {
            const { newData, filterNetworkId, appendData, hasMoreData, tagName } = action.payload;
            var PostUpdateData = postDataRenderData(filterNetworkId, state.postData, newData, tagName, state.filter_tags)
            PostUpdateData.afterUsedList = [...PostUpdateData.afterUsedList, PostUpdateData.appendData.after]
            PostUpdateData.hasMoreData = { ...PostUpdateData.hasMoreData, ...hasMoreData }
            let appendNewData = {
                after: appendData.after,
                networkID: appendData.networkID,
                heightEvent: appendData.heightEvent
                    ? appendData.heightEvent
                    : PostUpdateData.appendData.heightEvent,
                tagName: tagName
            };
            PostUpdateData.appendData = appendNewData;
            return {
                ...state,
                postData: PostUpdateData
            };

        }
        case ERROR_RESPONSE_WITH_MESSAGE: {
            var errorData = state.error;
            errorData.errorWithMessage = action.payload;

            return { ...state, error: errorData }
        }
        case GET_THEME_ERROR_SUCCESS: {
            var errorData = state.error;
            errorData.themeError = action.payload;
            return { ...state, error: errorData }
        }
        case PLAN_LIMIT: {
            var errorData = state.error;
            errorData.planLimit = action.payload;
            return { ...state, error: errorData }
        }
        case REMOVE_POST: {
            var updatePostData = state.postData;
            const { completeDataObject, completeData, filteredDataObject, postData } = updatePostData
            updatePostData.completeData = completeData.filter((item) => !action.payload.includes(item))
            updatePostData.filteredDataObject = Object.keys(filteredDataObject).reduce((acc, key) => {
                acc[key] = filteredDataObject[key].filter((item) => !action.payload.includes(item))
                return acc
            }, {})
            updatePostData.postData = postData.filter((item) => !action.payload.includes(item))
            updatePostData.completeDataObject = Object.keys(completeDataObject).reduce((acc, key) => {
                if (!action.payload.includes(key)) {
                    acc[key] = completeDataObject[key]
                }
                return acc
            }, {})
            return { ...state, postData: updatePostData }
        }
        case UPDATE_POST: {
            var addNewPostData = state.postData;
            addNewPostData.postData = state.postData.postData.reverse()
            var PostUpdateData = postDataRenderData(state.filterNetworkId, addNewPostData, action.payload, state.tagName, state.filter_tags)
            PostUpdateData.postData = PostUpdateData.postData.reverse()
            return {
                ...state,
                postData: PostUpdateData
            };
        }
        default:
            return { ...state }
    }

}


const APP_DATA = (response, heightEvent, widgetTheme, isNewRender) => {
    let Error = ERROR;
    var PostUpdateData = POST_DATA;
    let LoaderData = LOADER;
    let WallData = WALL_DATA;
    let WallID = WALL_ID;
    let LanguageData = LANGUAGE_SETTINGS;
    let WebFilter = WEB_FILTERS;
    let filter_tag = FILTER_TAGS;
    let reviewFilter = REVIEW_FILTERS;
    let isStudio = 0;
    let announcements = null

    const {
        feeds,
        planLimit,
        after,
        customPost,
        postData,
        webFilters,
        error_code,
        post_message,
        older_days,
        older_post,
        wall,
        reviewFilters,
        loader_type, loader_color1, loader_color2, filter_tags, is_studio_enabled
    } = response
    Error.planLimit = planLimit ? planLimit : false;
    if (wall && wall.UserRule && ((!isSocialWall && !isSocialEmbed && wall.UserRule.branding_lite && wall.UserRule.branding_lite === 1) || (isSocialWall && wall.UserRule.branding === 1) || ((isTagEmbed || isTagShop) && (wall.UserRule.branding == 1 || wall.UserRule.taggBranding == 1))) && (isTagEmbed || ![55].includes(wall.Personalization.widgetTheme))) PostUpdateData.isFreeAdsStatus = true;


    // else {
    //     if (wall && wall.UserDetail && wall.UserDetail.planId && ["1", "53"].includes(wall.UserDetail.planId) && ![55].includes(wall.Personalization.widgetTheme)) PostUpdateData.isFreeAdsStatus = true;
    // }
    LoaderData.loader = { loader_type, loader_color1, loader_color2 };
    LoaderData.isLoaderEnable = loader_type === 404 ? false : true;
    WallData = wall;
    WallID = wall.Wall.id;
    if (wall && wall.Banner && Object.keys(wall.Banner).length > 0 && wall.Banner.status === 1 && wall.Banner.bannerType == 6 && wall.Personalization.filterStatus === 0 && wall.Personalization.widgetTheme != 57) isReviewStatus = true

    if (isSocialWall) announcements = response.announcements

    if (WallData.ThemeRule.inheritStyles == 1) {
        if (isTagShop) {
            WallData["ProductSetting"]["Button"]['varient'] = '';
            WallData["ProductSetting"]["CatalogueTitle"]['varient'] = '';
            WallData["ProductSetting"]["Price"]['varient'] = '';
            WallData["ProductSetting"]["ProductTitle"]['varient'] = '';
        }
        WallData["ThemeRule"]["font_varient"] = '';
    }
    if (isTagShop) {
        WallData["Personalization"]["widgetTheme"] = WallData.Personalization.commerceTheme
        WallData["ThemeRule"]["hideContent"] = !WallData.ThemeRule.hideContent
        WallData["ThemeRule"]["radius"] = WallData.ThemeRule.roundEdge

        const { widgetTheme } = WallData.Personalization
        if (widgetTheme == 81 || widgetTheme == 55 || widgetTheme == 50) {
            WallData["ThemeRule"]["postHover"] = 1
        }
    }

    if (isSocialWall) {
        isStudio = is_studio_enabled
        WallData["Personalization"]["widgetTheme"] = WallData.Personalization.signageTheme
        WallData["Personalization"]["padding"] = WallData.ThemeRule.padding
        WallData["ThemeRule"]["radius"] = WallData.ThemeRule.borderRadius
        if (!isSocialEmbed) WallData["ThemeRule"]["autoPlay"] = 1
        if (window.location.href.includes("app=1")) {
            WallData["ThemeRule"]["autoPlay"] = 0
        }
        if ([76, 74, 77].includes(WallData.Personalization.signageTheme)) WallData["Personalization"]["postFeatured"] = 0
        if (!isSocialEmbed) WallData["Personalization"]["trimcontent"] = WallData.ThemeRule.trimcontent
        if (!isSocialEmbed) {

            WallData["Personalization"]["postFeatured"] = WallData.ThemeRule.postFeatured

        }
        if (WallData.ThemeRule.trimcontent == 1 && [66].includes(WallData.Personalization.signageTheme)) {
            WallData["ThemeRule"]["lineTrim"] = 3
        }

    }

    PostUpdateData.wallId = wall.Wall.id
    PostUpdateData.userId = wall.Wall.owner

    PostUpdateData.db_table = wall.UserDetail ? wall.UserDetail.db_table : null
    PostUpdateData.userDatabaseType = wall?.User?.userDatabaseType || "taggbox"


    if (isTagShop) {
        PostUpdateData.db_table = wall.UserPlan ? wall.UserPlan.db_table : null
        PostUpdateData.hotspot = wall?.ProductSetting?.Hotspot?.status || 0
    }


    // WallData["ThemeRule"]["authorColor"]=""
    // WallData["ThemeRule"]["fontColor"]=""
    // WallData["ThemeRule"]["fontColor"]=""
    if (postData && postData.length > 0 && postData[0] && Object.keys(postData[0]).length > 0 && postData[0].cta && Object.keys(postData[0].cta).length > 0) {
        WallData["ThemeRule"]["ctaData"] = postData[0].cta
    }

    LanguageData = {
        ...languageSettings(
            wall.Personalization.w_language,
            wall.Personalization.custom_lan_data
        )
    }

    Error.themeError = isTagEmbed ? { error_code: 0, post_message: "", older_days: 0, older_post: 0 } : {
        error_code: error_code,
        post_message: post_message,
        older_days: older_days,
        older_post: older_post,
    }


    filter_tag = filter_tags ? convertObjectToArray_Tags(filter_tags) : [];
    PostUpdateData.customPostData = customPost;
    PostUpdateData = postDataRenderData(PostUpdateData.filterNetworkId, PostUpdateData, postData, PostUpdateData.tagName, filter_tag, isNewRender)
    WebFilter = webFilters && webFilters.length > 0 ? convertObjectToArray(webFilters) : [];
    reviewFilter = reviewFilters && reviewFilters.length > 0 ? convertObjectToArray(reviewFilters) : [];

    PostUpdateData.appendData = {
        after: after,
        networkID: 0,
        heightEvent: heightEvent
            ? heightEvent
            : PostUpdateData.appendData.heightEvent,
    }


    return {
        error: Error,
        postData: PostUpdateData,
        loader: LoaderData,
        wall: WallData,
        wallID: WallID,
        languageSetting: LanguageData,
        webFilters: WebFilter,
        filter_tags: filter_tag,
        feeds: feeds,
        reviewFilters: reviewFilter,
        isStudio,
        announcements,
    }
}


var postDataRenderData = (filterNetworkId, PostUpdateData, newData, tagName, filter_tags, isNewRender) => {
    let filterId = filterNetworkId
        ? filterNetworkId
        : PostUpdateData.currentFilterNetworkId;
    if (!filterId) {
        filterId = 0;
    }


    let completeDataObject = isNewRender ? {} : PostUpdateData.completeDataObject;
    let filteredDataObject = isNewRender ? {} : PostUpdateData.filteredDataObject;
    let tagfilteredDataObject = isNewRender ? {} : PostUpdateData.tagfilteredDataObject;
    let completeData = isNewRender ? [] : PostUpdateData.completeData;
    let customPostData = PostUpdateData.customPostData ? PostUpdateData.customPostData : {};
    let freeAdsPosition = isNewRender ? [3,] : PostUpdateData.freeAdsPosition;


    if (Object.keys(newData).length) {
        Object.keys(newData).forEach((key) => {
            const item = newData[key];

            if (!completeDataObject[item.id]) {

                var refererUrl = PostUpdateData.originUrl;
                if (refererUrl && String(refererUrl).includes("widget/wall/index")) {
                    refererUrl = `https://widget.taggbox.com/${PostUpdateData.wallId}`
                }
                const description = encodeURIComponent(
                    item.content + (PostUpdateData.userId !== 30879 && !isTagEmbed && !isTagShop ? " via #taggbox" : '')
                );
                // item.content + 
                const shareUrl = encodeURIComponent(
                    SHARE_ROOT_URL +
                    window.btoa(
                        item.id + "---" + PostUpdateData.wallId + "---" + refererUrl + "---" + PostUpdateData.db_table + `${PostUpdateData?.userDatabaseType ? `---${PostUpdateData?.userDatabaseType}` : suiteTrack ? `---ugc-suite` : ``}`
                    )
                );

                const facebookShareUrl = (PostUpdateData.userId == 146945 && item.share && item.share.facebook) ? item.share.facebook : `https://www.facebook.com/sharer.php?u=${shareUrl}`;
                let twitterShareUrl = `https://twitter.com/intent/tweet?text=${description}`;
                if (item.type !== 1) {
                    twitterShareUrl = twitterShareUrl + "&url=" + shareUrl;
                }
                const plusGoogleShareUrl = `https://plus.google.com/share?url=${shareUrl}`;
                const linkedinShareUrl = `https://www.linkedin.com/shareArticle?mini=true&url=${shareUrl}`;
                item.shareDetail = {
                    shareUrl: SHARE_ROOT_URL +
                        window.btoa(
                            item.id + "---" + PostUpdateData.wallId + "---" + refererUrl + "---" + PostUpdateData.db_table + `${PostUpdateData?.userDatabaseType ? `---${PostUpdateData?.userDatabaseType}` : suiteTrack ? `---ugc-suite` : ``}`
                        ), description: (PostUpdateData.userId !== 30879 && !isTagEmbed && !isTagShop ? ` via #${PostUpdateData?.userDatabaseType ? PostUpdateData?.userDatabaseType : `taggbox`}` : '')
                }

                //item.content + 
                const itemShareUrl = isTagEmbed ? {} : {
                    facebook: facebookShareUrl,
                    twitter: twitterShareUrl,
                    linkedin: linkedinShareUrl,
                    google: plusGoogleShareUrl
                }
                item.share = {
                    ...item.share,
                    ...itemShareUrl
                };
                if (isPostExist(item.id)) item["isPopUp"] = true
                if (!item["postFileNew"]) item["postFileNew"] = item.file
                if (!item["filename"]) item["filename"] = item.file
                if (!item["postFile"]) item["postFile"] = item.file
                if (isTagEmbed) item["network"]["color"] = item.iconColor
                if (isTagShop && PostUpdateData.isFreeAdsStatus) item["postBranding"] = true
                //if (isTagShop && item.type == 2 || item.type == 4) item["hotspot"] = PostUpdateData.hotspot

                completeDataObject[item.id] = item;
            }
            if (item.network == null || Object.keys(item.network).length == 0) {
                item["network"] = {
                    "id": item.networkId ? item.networkId : 0,
                    "name": "",
                    "icon": "",
                    "color": "",
                    "description": "",
                    "status": 1
                }
            }
            if (filteredDataObject[item.network.id]) {

                if (!filteredDataObject[item.network.id].includes(item.id)) {
                    if (isReviewStatus) {
                        if (item.rating > 0) filteredDataObject[item.network.id].push(item.id);
                    }
                    else filteredDataObject[item.network.id].push(item.id);
                }

            } else {
                if (isReviewStatus) {
                    if (item.rating > 0) filteredDataObject[item.network.id] = [item.id];
                }
                else filteredDataObject[item.network.id] = [item.id];
            }
            if (filter_tags && filter_tags.length > 0) {
                if (item.tags && String(item.tags) && item.tags.length > 0) {
                    let updatedFilterTag = String(item.tags).toLocaleLowerCase()
                    filter_tags.map((filterTagName) => {
                        if (updatedFilterTag.includes(String(filterTagName.tagName).toLocaleLowerCase())) {
                            if (tagfilteredDataObject[String(filterTagName.tagName).toLocaleLowerCase()]) {
                                //if(isPreRenderData){
                                if (!tagfilteredDataObject[String(filterTagName.tagName).toLocaleLowerCase()].includes(item.id)) {
                                    tagfilteredDataObject[String(filterTagName.tagName).toLocaleLowerCase()].push(item.id);
                                }
                                // } else {
                                //filteredDataObject[item.network.id].push(item.id);
                                //}
                            } else {
                                tagfilteredDataObject[String(filterTagName.tagName).toLocaleLowerCase()] = [item.id];
                            }
                        }
                    })
                }



            }


            if (!completeData.includes(item.id)) completeData.push(item.id);

            initialState.networkData[key] = { networkId: item.network.id, networkName: item.network.name }
        });

    }

    let postData = filterId && filteredDataObject[filterId]
        ? filteredDataObject[filterId]
        : filterId === 0
            ? completeData
            : [];

    postData = tagName && tagName.length > 0 && tagfilteredDataObject[String(tagName).toLocaleLowerCase()] ? tagfilteredDataObject[String(tagName).toLocaleLowerCase()] : postData;


    if (customPostData && Object.keys(customPostData).length) {
        Object.keys(customPostData).forEach((key) => {
            let item = customPostData[key];
            if (isTagShop && PostUpdateData.isFreeAdsStatus && item) item["postBranding"] = true
            const { status, after, every } = item;
            if (status === 1 && every != 0) {
                let customPostIndex = parseInt(after);
                for (
                    customPostIndex;
                    customPostIndex <= completeData.length;
                    customPostIndex = customPostIndex + parseInt(every)
                ) {
                    if (completeData.length > customPostIndex) {
                        if (completeData[customPostIndex] !== item.id)
                            completeData.splice(customPostIndex, 0, item.id);
                        if (!completeDataObject[item.id]) {
                            if (item.network == null || Object.keys(item.network).length == 0) {
                                item["network"] = {
                                    "id": item.networkId ? item.networkId : 0,
                                    "name": "",
                                    "icon": "",
                                    "color": "",
                                    "description": "",
                                    "status": 1
                                }
                            }
                            completeDataObject[item.id] = item;
                        }
                    }
                }
            }
            else {
                if (isTagShop) {
                    if (!completeData.includes[item.id]) {
                        completeData.push(item.id);
                        if (!completeDataObject[item.id]) {
                            if (item.network == null || Object.keys(item.network).length == 0) {
                                item["network"] = {
                                    "id": item.networkId ? item.networkId : 0,
                                    "name": "",
                                    "icon": "",
                                    "color": "",
                                    "description": "",
                                    "status": 1
                                }
                            }
                            completeDataObject[item.id] = item;

                        }
                    }
                }
            }
        });
    }


    return {
        ...PostUpdateData,
        completeDataObject,
        completeData,
        postData: postData,
        filteredDataObject: filteredDataObject,
        tagfilteredDataObject: tagfilteredDataObject,
        preRender: PostUpdateData.preRender,
        freeAdsPosition
    };

}