let imageQueue = [];
let expirePostData = {};
export let dataSource;
export let dataRegion;
let messageSent = false;
var isUpdateFrom = window.isTagEmbd || isTagEmbed || String(window.location.href).includes("widget.tagembed") || String(window.location.href).includes("widget-test.tagembed") ? { userfrom: "tagembed" } : { userfrom: "taggbox" }

const getHashTagParam = (rID, wallID) => {
    const parsed = queryString.parse(window.location.search);
    var shopParmas = {}
    if (isTagShop && (window.location.href.includes("test.tagshop.ai/w/") || window.location.href.includes("widget-test.tagshop.ai") || window.location.href.includes("widget.tagshop.ai") || (parsed && parsed.website) || (parsed && parsed.region))) {
        dataSource = `website`
        shopParmas = { Website: 1 }
        if ((parsed && parsed.wall)) {
            dataSource = ""; shopParmas = {};
        }
        if ((parsed && parsed.region)) dataRegion = parsed.region
    }
    if (isSocialWall) {
        if (parsed && parsed.wallKey) shopParmas["wallKey"] = parsed.wallKey
    }
    var header_tags = { ...shopParmas, ...isUpdateFrom }; var tagEmbedEncode = isTagEmbed && !wallIdEncodeByPass.includes(wallID) ? { responseType: 'arraybuffer' } : {}
    var updateContainerID = rID ? `#co_${rID}` : ".taggbox"
    if (parsed.tags) {
        const tags = parsed.tags;
        const updateTags = { tag: tags.includes("true") ? 1 : 0 }
        let updateDataKeywords = {}
        if (parsed["data-keywords"]) {
            updateDataKeywords = { keywords: `${String(decodeURIComponent(parsed["data-keywords"]))}` }
        }
        if (parsed["keywords"]) {
            updateDataKeywords = { keywords: `${String(decodeURIComponent(parsed["keywords"]))}` }
        }
        header_tags = { ...header_tags, ...updateTags, ...updateDataKeywords };
    }
    else if (parsed.website) {
        dataSource = `website`
        header_tags["Website"] = 1
        if (dataRegion) header_tags["region"] = dataRegion
    }
    else if (document.querySelector(updateContainerID) != null) {
        const taggbox = document.querySelector(updateContainerID);
        if (taggbox) {
            if (taggbox.getAttribute('data-tags')) {
                const dataTags = taggbox.getAttribute('data-tags');
                let updateDataTags = { tag: dataTags.includes("true") ? 1 : 0 }

                const dataKeywords = taggbox.getAttribute('data-keywords');
                let updateDataKeywords = {}
                if (dataKeywords) {
                    updateDataKeywords = { keywords: `${String(decodeURIComponent(dataKeywords))}` }
                }
                header_tags = { ...updateDataTags, ...updateDataKeywords, ...isUpdateFrom };
            }
            if (isTagShop && (taggbox.getAttribute('data-source') || window.location.href.includes("test.tagshop.ai/w/") || window.location.href.includes("app.tagshop.ai/w/") || taggbox.getAttribute('data-region'))) {
                if (taggbox.getAttribute('data-source')) {
                    dataSource = `website`
                    header_tags["Website"] = 1
                }
                if (taggbox.getAttribute('data-region')) {
                    dataRegion = taggbox.getAttribute('data-region')
                    header_tags["region"] = taggbox.getAttribute('data-region')
                }
            }
        }

    }
    return { headers: header_tags, ...tagEmbedEncode }
}
const getThemeId = (ID) => {

    var themeId = "";
    var updateContainerID = ID ? `#co_${ID}` : ".taggbox"
    var themeUrl = window.location.href.includes("theme-id");

    if (document.querySelector(updateContainerID) != null) {
        const taggbox = document.querySelector(updateContainerID);

        if (taggbox) {
            if (taggbox.getAttribute('theme-id')) {
                themeId = taggbox.getAttribute('theme-id');
            }
        }
    } else if (themeUrl) {
        themeId = getParameter('theme-id');
    }

    return themeId
}
const getAttributes = (ID) => {

    var returnData = { isStatus: false, mUrl: "" }
    var updateContainerID = ID ? `#co_${ID}` : ".taggbox"

    if (document.querySelector(updateContainerID) != null) {
        const taggbox = document.querySelector(updateContainerID);
        if (taggbox) {
            if (taggbox.getAttribute('data-filter-type')) {
                returnData["isStatus"] = true
                returnData["mUrl"] = `${taggbox.getAttribute('data-filter-type')}/${encodeURIComponent(taggbox.getAttribute('data-filter-id'))}`
                window.dataFilterType = taggbox.getAttribute('data-filter-type')
            }
        }
    }
    return returnData
}

export const UseRequest = () => {
    if (isTagEmbed) return axios
    else return new HttpClient()
}
export const themeByWallID = (Id, heightEvent) => {
    return (dispatch) => {
        try {
            SocialFonts()

            var isEmbedLoad = window.location.href.includes("editor=1") ? false : window.editor ? false : true

            if (rID) dispatch({ type: RENDER_ID, payload: rID })

            const themeId = getThemeId(rID);
            var updateUrl = `${GET_WIDGET_DATA_API_PATH}${Id}`
            if (isTagShop) {
                const { isStatus, mUrl } = getAttributes(rID)
                //console.log(isStatus, mUrl)
                if (isStatus) updateUrl = `${updateUrl}/${mUrl}`
            }

            //isTagEmbed ? `${ GET_WIDGET_WALL_PATH }${ Id }.json` : `${ GET_WIDGET_DATA_API_PATH }${ Id }`
            UseRequest().get(isTagEmbed ? `${GET_WIDGET_WALL_PATH}${Id}.json` : updateUrl, getHashTagParam(rID, Id))
                .then((apiResponse) => {
                    var response = {}
                    if (isTagEmbed && !wallIdEncodeByPass.includes(Id)) {
                        const arrayBuffer = apiResponse.data;

                        const inflatedData = pako.inflate(new Uint8Array(arrayBuffer), { to: 'string' });
                        response = { data: JSON.parse(inflatedData) };
                        if (window.location.href.includes("console=true")) console.log("api response", response)
                    }
                    else {
                        if (isTagShop) {
                            const newDATA_response = {
                                data: apiResponse?.data?.responseData,
                                response_code: apiResponse?.data?.responseCode,
                                status: apiResponse?.data?.responseStatus,
                            }
                            response = newDATA_response
                        }
                        else response = apiResponse
                    }


                    if (parseInt(response.data.response_code) == 404) {
                        dispatch({
                            type: ERROR_RESPONSE_WITH_MESSAGE,
                            payload: response.data,
                        });
                        throw new Error(response.data.message);

                    }
                    else if (suiteTrack || (window.editor && !isTagEmbed) || (isTagEmbed && window.editor && response.data.response_code == 200) || response.data.visitorLimit === 0 || isSocialWall || isSocialEmbed ? response.data.response_code == 200 : response.response_code == 200) {
                        const isRedis = response.data.status && String(response.data.status).includes("redis") ? false : true
                        if (isEmbedLoad && isRedis) urlTracking(Id, response.data.wall.Wall.owner, isTagEmbed ? response.data.wall.Wall.productId : 0);
                        dispatch({ type: GET_APP_DATA, payload: response.data, heightEvent: heightEvent, themeId: themeId });
                        if (window.location.href.includes("onsite-upload") || isEmbed) dispatch({ type: ONSITE_TOKEN_UPDATE, payload: response.data.onsite_token });
                        if (isSocialWall && !isEmbed && (response?.data?.is_studio_enabled == 1 && window.location.href.includes('studio=1') || response?.data?.is_studio_enabled == 0)) socialWallSocket(dispatch)

                        if (isTagShopEditor && window.location.href.includes("editor=1")) {
                            if (!messageSent) {
                                const newData = { ...response.data, url: window.location.href }
                                window.parent.postMessage({ type: "sreloadWebsite", data: JSON.stringify(newData) }, "*");
                                messageSent = true;
                            }
                        }


                    } else {
                        if (isTagEmbed) {
                            dispatch({
                                type: ERROR_RESPONSE_WITH_MESSAGE,
                                payload: response.data,
                            });
                        }
                        else {
                            dispatch({
                                type: ERROR_RESPONSE_WITH_MESSAGE,
                                payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response.data },
                            });
                        }
                        if (isTagShopEditor && window.location.href.includes("editor=1")) {
                            if (!messageSent) {
                                const newData = { url: window.location.href }
                                window.parent.postMessage({ type: "sreloadWebsite", data: JSON.stringify(newData) }, "*");
                                messageSent = true;
                            }
                        }
                        //throw new Error("");
                    }

                })
                .catch((error) => {
                    if (isTagEmbed) {
                        onRequestFail(dispatch, Id, heightEvent)
                    }
                    else {
                        if (!isTagShop) {
                            const { response } = error;
                            if (response?.status === 404 || (isSocialWall && response?.status === 401)) {
                                if (parseInt(response.status) == 404 || (isSocialWall && response?.status === 401)) {
                                    dispatch({
                                        type: ERROR_RESPONSE_WITH_MESSAGE,
                                        payload: response.data,
                                    });
                                }
                            }
                            dispatch({ type: GET_LOADER_ERROR });
                        }
                    }
                });
        }
        catch (ex) {
            const { response } = ex;
            dispatch({
                type: ERROR_RESPONSE_WITH_MESSAGE,
                payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response.data },
            });
            throw new Error("");
        }
    };
};

export const onRequestFail = (dispatch, Id, heightEvent) => {
    try {
        var isEmbedLoad = window.location.href.includes("editor=1") ? false : window.editor ? false : true

        if (rID) dispatch({ type: RENDER_ID, payload: rID })
        const themeId = getThemeId(rID);

        var updateUrl = `${GET_WIDGET_DATA_API_PATH}${Id}`
        if (isTagShop) {
            const { isStatus, mUrl } = getAttributes(rID)
            if (isStatus) updateUrl = `${updateUrl}/${mUrl}`
        }


        UseRequest().get(updateUrl, getHashTagParam(rID, Id))
            .then((apiResponse) => {
                var response = {}
                if (isTagEmbed && !wallIdEncodeByPass.includes(Id)) {
                    const arrayBuffer = apiResponse.data;
                    const inflatedData = pako.inflate(new Uint8Array(arrayBuffer), { to: 'string' });
                    response = { data: JSON.parse(inflatedData) };
                    if (window.location.href.includes("console=true")) console.log("api response", response)
                }
                else response = apiResponse
                if (parseInt(response.data.response_code) == 404) {
                    dispatch({
                        type: ERROR_RESPONSE_WITH_MESSAGE,
                        payload: response.data,
                    });
                    throw new Error(response.data.message);
                }
                else if (suiteTrack || window.editor || response.data.visitorLimit === 0 || response.data.response_code == 200) {
                    if (isEmbedLoad) urlTracking(Id, response.data.wall.Wall.owner, isTagEmbed ? response.data.wall.Wall.productId : 0);
                    dispatch({ type: GET_APP_DATA, payload: response.data, heightEvent: heightEvent, themeId: themeId });
                    if (window.location.href.includes("onsite-upload") || isEmbed) dispatch({ type: ONSITE_TOKEN_UPDATE, payload: response.data.onsite_token });

                } else {
                    if (isTagEmbed) {
                        dispatch({
                            type: ERROR_RESPONSE_WITH_MESSAGE,
                            payload: response.data,
                        });
                    }
                    else {
                        dispatch({
                            type: ERROR_RESPONSE_WITH_MESSAGE,
                            payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response.data },
                        });
                    }
                    //throw new Error("");
                }

            })
            .catch((error) => {
                const { response } = error;
                if (response && response.status && response.status == 404) {
                    if (parseInt(response.status) == 404) {
                        dispatch({
                            type: ERROR_RESPONSE_WITH_MESSAGE,
                            payload: response.data,
                        });
                    }
                }
                dispatch({ type: GET_LOADER_ERROR });
            });
    }
    catch (ex) {
        const { response } = ex;
        dispatch({
            type: ERROR_RESPONSE_WITH_MESSAGE,
            payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response.data },
        });
        throw new Error("");
    }
}
