const customLanguageParse = (data) => {
    let removeCha = data.slice(4, data.length).replace("{", "").replace("}", "");
    let splitWith = removeCha.split(";");
    let getData = [];
    let getKey = [];
    let getCompleteData = {};
    splitWith.map((item, index) => {
        let stringReplace = item.replace('"', "").replace('"', "");
        let key = isTagEmbed ? stringReplace.slice(4) : stringReplace.slice(4, stringReplace.length);
        let isCheck = isTagEmbed ? key != "0" : key != ""
        if (isCheck) {
            let valueKey = key.replace(":", "");
            if (index % 2 == 0) getKey.push(valueKey); else getData.push(valueKey);
        }
    });
    getKey.map((item, index) => Object.assign(getCompleteData, { [item]: getData[index] }));
    return { getData, getKey, getCompleteData };
};
