/*******************tagembed Image expire code***********************/



export const dataUpdate = async (e) => {
    var items = document.querySelectorAll("img");
    e.target.src = `https://cdn.tagembed.com/app/image/blur-img.jpg`;
    e.target.setAttribute("data-preload", "1");
    for (var i = items.length; i--;) {
        var img = items[i];
        const dataPreload = img.getAttribute("data-preload")
        if (dataPreload == 1) await loopUpdateDataImage(img)
    }
}
var popimg = [];
export const loopUpdateDataImage = async (e) => {
    const { wall } = store.getState().appData;
    const networkId = e.getAttribute("data-network")
    const wallId = e.getAttribute("data-wall-id")
    const postId = e.getAttribute("data-item-id")
    const planId = wall.UserRule.planId//e.getAttribute("data-plan-id")
    const feedId = e.getAttribute("data-feed-id")
    if (networkId) {
        if ((networkId == 2 || networkId == 18 || networkId == 3 || networkId == 10 || networkId == 31) && e.getAttribute("data-load") == 0) {
            await axios.post('https://api.tagembed.com/app/expost/updateexpirypost', {
                wallId: wallId,
                postId: postId,
                networkId: networkId,
                planId: planId,
                feedId: feedId,
            }).then(({ data }) => {
                if (data.status) e.src = data.data.imageUrl;
                else e.onerror = null;
                e.attributes["data-load"].value = "1";
                e.setAttribute("data-preload", "0")
            }).catch((error) => {
                e.setAttribute("data-preload", "0")
                e.attributes["data-load"].value = "1";
                e.onerror = null;
            })
        } else {
            e.setAttribute("data-preload", "0")
            e.attributes["data-load"].value = "1";
            e.onerror = null;
        }
    }
}




export const dataUpdates = e => {
    const { wall } = store.getState().appData;
    const networkId = e.target.getAttribute("data-network")
    const wallId = e.target.getAttribute("data-wall-id")
    const postId = e.target.getAttribute("data-item-id")
    const planId = wall.UserRule.planId//e.target.getAttribute("data-plan-id")
    const popId = e.target.getAttribute("data-pop-id")
    const feedId = e.target.getAttribute("data-feed-id")
    e.target.src = `https://cdn.tagembed.com/app/image/blur-img.jpg`;
    if (networkId && !(popimg.includes(popId))) {
        popimg.push(popId);
        if ((networkId == 2 || networkId == 18 || networkId == 3 || networkId == 10 || networkId == 31) && e.target.getAttribute("data-load") == 0) {
            axios.post('https://api.tagembed.com/app/expost/updateexpirypost', {
                wallId: wallId,
                postId: postId,
                networkId: networkId,
                planId: planId,
                popId: popId,
                feedId: feedId,
            }).then(({ data }) => {
                if (data.status) e.target.src = data.data.imageUrl;
                else e.target.onerror = null;
                e.target.setAttribute("data-load", "1")
                e.target.setAttribute("data-preload", "0")
            }).catch((error) => {
                e.target.setAttribute("data-preload", "0")
                e.target.setAttribute("data-load", "1")
                e.target.onerror = null;
            })
        } else e.target.setAttribute("data-preload", "1")
    }
    else e.target.setAttribute("data-preload", "1")
}


export const loopUpdateDataVideo = async (e, ppId) => {
    const { wall } = store.getState().appData;
    const newData = document.querySelector(`.video${ppId}`)
    if (newData) {

        const dataLoad = newData.getAttribute("data-load")
        const networkId = newData.getAttribute("data-network")
        const wallId = newData.getAttribute("data-wall-id")
        const postId = newData.getAttribute("data-item-id")
        const planId = wall.UserRule.planId//e.target.getAttribute("data-plan-id")
        const feedId = newData.getAttribute("data-feed-id")
        // e.target.src = `https://media.tagembed.com/post/97830/141472/191788/17959246868565139.mp4`;
        if (networkId && dataLoad == 0) {
            if (networkId == 2 || networkId == 18 || networkId == 3 || networkId == 31 || networkId == 10) {
                await axios.post('https://api.tagembed.com/app/expost/updateexpirypost', {
                    wallId: wallId,
                    postId: postId,
                    planId: planId,
                    networkId: networkId,   // new code
                    feedId: feedId
                }).then(({ data }) => {
                    if (data.status) {
                        e.target.src = data.data.videoUrl;
                        e.target.poster = data.data.imageUrl;
                    }
                    else e.onerror = null;
                    newData.setAttribute("data-load", 1)
                }).catch((error) => {
                    e.onerror = null;
                    newData.setAttribute("data-load", 1)
                })
            }
        }
    }

}

export const showHotspot = (data) => {
    return (dispatch) => {
        dispatch({ type: "SHOW_HOTSPOT", payload: data });
    };
};

export const onUpdateEditor = (updateData) => {
    try {
        return (dispatch) => {
            const { error, loader, WebFilter } = store.getState().appData
            const { after, postData, wall, customPost } = updateData;
            const data = {
                after, postData, wall, planLimit: error.planLimit, ...error.themeError, error_code: 0, customPost: customPost ? customPost : [], ...loader.loader, visitorLimit: 0,
                response_code: 200, status: "success redis", webFilters: WebFilter
            }
            if (isTagShopEditor) getCurrentTheme(wall?.Personalization?.commerceTheme)
            else getCurrentTheme(wall?.Personalization?.widgetTheme)
            dispatch({ type: GET_APP_DATA, payload: data, heightEvent: null, themeId: "" });
        };
    }
    catch (ex) {
        console.log("onUpdateEditor", ex)
    }
};

export const updateIntraction = async (data) => new HttpClient().post(`${INTRACTIVE_SOCIALWALLS_UPDATE}`, data)



export const SocialCustomStyle = (styleData) => {
    try {
        const styleId = "tb_socialfonts";
        if (!document.getElementById(styleId)) {
            const style = document.createElement('style');
            style.type = 'text/css';
            style.id = styleId;
            style.innerHTML = styleData
            document.head.appendChild(style)
        }
    }
    catch (e) {
    }
}
export const SocialFonts = () => {
    const version = 5
    const WebSocialFonts = `@font-face { font-family: 'WebSocialFonts'; src: url('${CloudUrl}/web-fonts/fonts-icon/WebSocialFonts.ttf?var=${version}') format('truetype'), url('${CloudUrl}/web-fonts/fonts-icon/WebSocialFonts.woff?var=${version}') format('woff'), url('${CloudUrl}/web-fonts/fonts-icon/WebSocialFonts.svg?var=${version}') format('svg'); font-weight: normal; font-style: normal; font-display: swap;`
    SocialCustomStyle(WebSocialFonts)
}

export const loadSiteFeed = () => {
    if (addfeedload) {
        if (!messageSent) {
            const newData = { url: window.location.href }
            window.parent.postMessage({ type: "reloadWebsite", data: JSON.stringify(newData) }, "*");
            messageSent = true;
        }
    }
}