
import axios from 'axios';
import { APIHOST, AUTHORIZATION_TOKEN } from './api'

var editor = window.editor ? { editor: 1 } : window.location.href.includes("editor=1") ? { editor: 1 } : {}
var ugcSuite = window.ugcSuite ? { ugcSuite: 1 } : window.location.href.includes("ugcSuite=1") ? { ugcSuite: 1 } : {}
var getdisplaywall = window.isSocialEmbed ? { getdisplaywall: 1, ispreview: 1 } : window.isSocial && !window.editor ? { ispreview: 1 } : {}
const requestInterceptor = (config) => {
    return {
        ...config,
        headers: {
            'authorization': `Bearer ${AUTHORIZATION_TOKEN}`,
            ...config.headers,
            productdomain: `taggbox.com`,
            requestDomain: window.location.href,
            isLiteCode: true,
            ...editor,
            ...ugcSuite,
            ...getdisplaywall

        },
    }
};

class HttpClient {
    constructor() {
        const options = { baseURL: APIHOST };
        const instance = axios.create(options);
        instance.interceptors.request.use(requestInterceptor);
        axios.interceptors.response.use(
            function (response) {
                return response;
            },
            function (error) {
                return Promise.reject(error);
            }
        );
        return instance;
    }
}

export default HttpClient;
