export const bestFitRow = (windowWidth, wall) => {
    let cardNumber = wall.ThemeRule.numberOfCoumn;
    try {
        const { postData } = store.getState().appData;
        const postLength = postData.postData.length
        const fitRow = wall.Personalization.fitRow;

        if (fitRow == 1) {
            var minWidth = parseInt(windowWidth / wall.Personalization.minimumPostWidth);
            cardNumber = postLength >= minWidth ? wall.ThemeRule.numberOfCoumn : postLength
        }
        return cardNumber
    }
    catch (ex) {
        return cardNumber
    }
}

