    let cardNumber = null; let adjustWidth = null; let themeId = isTagEmbed ? [3, 4, 20, 52, 5, 19, 50, 59] : isSocialWall && !isSocialEmbed ? [70, 64] : isSocialWall && isSocialEmbed ? [70, 64] : [3, 4, 5, 20, 19, 62, 79, 83]

