export const bytesToSize = b => { const s = ['Bytes', 'KB', 'MB', 'GB', 'TB']; if (!b) return '0 Byte'; const i = parseInt(Math.floor(Math.log(b) / Math.log(1024))); return { size: Math.round(b / Math.pow(1024, i), 2), sizeText: s[i] }; };
