export const removeDuplicateStrings = (arr) => {
    try {
        return [...new Set(arr)];
    }
    catch (ex) {
        return arr
    }
}
export const STRING_TO_URL_CONVERT = (string) => {
    try {
        var addString = string;
        if (string && string && string.includes("http")) {
            const urls = string.match(/(((ftp|https?):\/\/)[\-\w@:%_\+.~#?,&\/\/=]+)/g);
            const uniqueArray = removeDuplicateStrings(urls)
            if (uniqueArray) {
                uniqueArray.forEach((url) => {
                    addString = addString.replaceAll(url, `<a class="text_links_" role="link" target="_blank" href="${url}">${url}</a>`);
                });
                return addString;
            }
            else return addString;
        }
        else return addString;
    }
    catch (ex) {
        return string;
    }
}
