export const isHex = (color) => {
    return color.startsWith('#');
}
export const hexToRgb = (hex) => {
    if (isHex(hex)) {
        hex = hex.replace(/^#/, '');
        var bigint = parseInt(hex, 16);
        var r = (bigint >> 16) & 255;
        var g = (bigint >> 8) & 255;
        var b = bigint & 255;
        return { r: r, g: g, b: b };
    } else {
        const matches = hex.match(/\d+/g);
        const [r, g, b] = matches && matches.length > 0 ? matches.map(Number) : [0, 0, 0];
        return { r: r, g: g, b: b };
    }
}
export const getBrightness = (color) => {
    var rgb = hexToRgb(color)
    return (rgb.r * 299 + rgb.g * 587 + rgb.b * 114) / 1000;
}

export const colorDistanceLight = (color) => {
    var rgb = hexToRgb(color)
    var distance = Math.sqrt(Math.pow(255 - rgb.r, 2) + Math.pow(255 - rgb.g, 2) + Math.pow(255 - rgb.b, 2));
    return distance <= 30;
}

export const colorDistanceDark = (color) => {
    var rgb = hexToRgb(color)
    const deltaR = rgb.r - 50.0;
    const deltaG = rgb.g - 50.0;
    const deltaB = rgb.b - 50.0;
    return Math.sqrt(deltaR * deltaR + deltaG * deltaG + deltaB * deltaB) < 50;
}

export const isEmptyString = (txtString) => {
    if (txtString && String(txtString).length > 0) return false
    else return true
}

export const getTextColor = (textColor, bgColor) => {
    if (textColor.startsWith('rgba')) {
        var textColor = '#000000';
    }
    const isWhite = color => {
        return (color.includes('ffffff')) || (color.includes("255, 255, 255")) || (color == '#fff') ? true : false
    }
    const isBlack = color => {
        return (color.includes('000000')) || (color.includes("0, 0, 0")) || (color == '#000') ? true : false
    }

    var similarWhite = colorDistanceLight(textColor)
    var similarDark = colorDistanceDark(textColor)

    var similarWhiteBg = colorDistanceLight(bgColor)
    var similarDarkBg = colorDistanceDark(bgColor)

    if ((similarWhite && similarWhiteBg) || (isWhite(textColor) && isWhite(bgColor))) return '#000000';
    else if ((similarDark && similarDarkBg) || (isBlack(textColor) && isBlack(bgColor))) return '#ffffff';
    else return textColor;

}


