export const kFormatter = (value) => {
    if (value < 1e3) return value;
    if (value >= 1e3 && value < 1e6) return +(value / 1e3).toFixed(0) + "K";
    if (value >= 1e6 && value < 1e9) return +(value / 1e6).toFixed(0) + "M";
    if (value >= 1e9 && value < 1e12) return +(value / 1e9).toFixed(0) + "B";
    if (value >= 1e12) return +(value / 1e12).toFixed(0) + "T";
}

