export const GET_IMAGE_SIZE = (url) => {
    return new Promise((resolve, reject) => {
        const ImageEle = new Image();

        ImageEle.onload = () => {
            resolve({ width: ImageEle.naturalWidth, height: ImageEle.naturalHeight });
        };

        ImageEle.onerror = (event) => {
            reject(new Error(`Image loading error: ${event.message}`));
        };

        ImageEle.src = url; // Start loading the image
    });
};

