export const announcementsUtils = (timeStamp, showAt, endAt, every, anncKey) => {
    try {
        let repeatOnce = null;
        let timeInterval = getTimeFromDate(getConvertToDate(showAt)) / 1000.0;
        let aEndTime = getTimeFromDate(getConvertToDate(endAt)) / 1000.0;
        if (every != 0) repeatOnce = (timeStamp - timeInterval + anncKey * 10) % (every * 60); //repeat
        else repeatOnce = (timeStamp - timeInterval - 23400 + anncKey * 10);//once
        return { repeatOnce, timeInterval, aEndTime };
    }
    catch (ex) {
        return { repeatOnce: 0, timeInterval: 0, aEndTime: 0 };
    }
}
