export const getConvertToDate = (date) => {

    try {
        if (navigator.userAgent.indexOf("Safari") !== -1) {
            let dateData = new Date(date);
            let dateParts = dateData.split(/[- :T]/);
            let [year, month, day, hour, minute, second] = dateParts.map(Number);
            return new Date(year, month - 1, day, hour, minute, second);
        } else {
            return new Date(date);
        }
    }
    catch (ex) {
        return new Date(date);
    }
};
