
import { POST_DEFAULT_HEIGHT } from './reducersKeys'
const initialState = { isShowPopUp: false, data: null, onSiteIsShowPopUp: false, reportStatus: false, shareStatus: false, reportData: null, shareData: null, isShowMoreLoading: false, isFilterLoading: false }
export default function reducer(state = initialState, action) {
  switch (action.type) {
    case POST_DEFAULT_HEIGHT: { return { data: action.payload, status: true, manage_height: action.payload !== 0 ? true : false } }
    default:
      return { data: 0, status: false }
  }
}