export const tagEmbedRender = () => {
    if (String(window.location.href).includes("tagbox=true")) return false
    // else if (window.isTagEmbd) return true
    // else if (String(window.location.href).includes("widget.tagembed")) return true
    //else if (String(window.location.href).includes("widget-test.tagembed")) return true
    else if (String(window.location.href).includes("widgets.tagembed")) return true
    else return false
}

export const tagShopRender = () => {
    if (String(window.location.href).includes("tagbox=true")) return false
    else if (window.isTagShop) return true
    else if (String(window.location.href).includes("app.tagshop.ai")) return true
    else if (String(window.location.href).includes("test.tagshop.ai")) return true
    else if (String(window.location.href).includes("widget-test.tagshop.ai")) return true
    else if (String(window.location.href).includes("widget.tagshop.ai")) return true
    else return false


}
export const tagSocialRender = () => {
    if (String(window.location.href).includes("tagbox=true")) return false
    else return false

}

